.header-page {
  color: #dfdfdf;
  font-family: Montserrat, serif;
  font-size: 16px;
  font-weight: 500;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
}

.header-page:focus, .header-page:hover {
  color: #fff;
}

.header-page:hover, .header-page:after {
  opacity: 1;
  transform: scaleX(1);
}

.header-page:after {
  content: "";
  opacity: 0;
  background-color: #fff;
  border-radius: 2px;
  width: 100%;
  height: 5px;
  transition: transition, opacity, all .25s cubic-bezier(.4, 0, .2, 1);
  display: block;
  position: absolute;
  bottom: -30px;
  left: 0;
  transform: scaleX(0);
}

